import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { ContentHeaderModule } from 'app/layout/components/content-header/content-header.module';
import { CoreCommonModule } from '@core/common.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SweetalertComponent } from 'app/shared/sweetalert/sweetalert.component';
import { MainDesignComponent } from './components/main-design/main-design.component';
import { StichComponent } from './components/stich/stich.component';
import { CuffComponent } from './components/cuff/cuff.component';
import { CollarComponent } from './components/collar/collar.component';
import { PlacketComponent } from './components/placket/placket.component';
import { ColorsComponent } from './components/colors/colors.component';

const routes: Routes = [
  {
    path: "",
    component: MainDesignComponent,
    children:[
    { path: "", redirectTo: "stich-type", pathMatch: "full" },
    {
      path: "stich-type",
      component: StichComponent,
      data: { animation: "stich-type" },
    }, 
    {
      path: "stich-type/:id",
      component: StichComponent,
      data: { animation: "stich-type-id" },
    },
    {
      path: "cuff-type",
      component: CuffComponent,
      data: { animation: "cuff-type" },
    }, 
    {
      path: "cuff-type/:id",
      component: CuffComponent,
      data: { animation: "cuff-type-id" },
    },
    {
      path: "collar-type",
      component: CollarComponent,
      data: { animation: "collar-type" },
    }, 
    {
      path: "collar-type/:id",
      component: CollarComponent,
      data: { animation: "collar-type-id" },
    },
    {
      path: "placket-type",
      component: PlacketComponent,
      data: { animation: "placket-type" },
    }, 
    {
      path: "placket-type/:id",
      component: PlacketComponent,
      data: { animation: "placket-type-id" },
    },
    {
      path: "color-type",
      component: ColorsComponent,
      data: { animation: "color-type" },
    }, 
    {
      path: "color-type/:id",
      component: ColorsComponent,
      data: { animation: "color-type-id" },
    },
    
    ]
  },
  
]

@NgModule({
  declarations: [
    MainDesignComponent,
    StichComponent,
    CuffComponent,
    CollarComponent,
    PlacketComponent,
    ColorsComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    ContentHeaderModule,
    CoreCommonModule,
    FormsModule,
    ReactiveFormsModule,
  ],
  providers: [SweetalertComponent],

})
export class DesignModule { }
