import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Route, Router } from "@angular/router";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";
import { StichTypeService } from "../../service/stichType.service";


@Component({
  selector: 'app-stich',
  templateUrl: './stich.component.html'
})
export class StichComponent implements OnInit {
  public contentHeader: Object;
  public tableDataArray = [];
  public stichTypeForm: FormGroup;
  public submitted = false;
  public editable = false;
  public id;

  constructor(
    private _sw: SweetalertComponent,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private _serviceData: StichTypeService,
    private _router: Router
  ) {}

  onSubmit(formData) {

    this.submitted = true;
    if (this.stichTypeForm.invalid) {
      return;
    }

    if (this.editable) {
      this.updateFormDetail(formData);
    } else {
      this.insertFormDetail(formData);
    }
  }

  insertFormDetail(formData) {
    this._serviceData.create(formData).subscribe(
      (res) => {
        this._sw.fire("success", "Stich Type", `Record Successfully Add..`);
        this.getAllDetail();
        this.submitted = false;
        this.stichTypeForm.reset();
      },
      (err) => {
        this._sw.fire("error", "Stich Type", err.error.message);
      }
    );
  }

  
  navigateToBack() {
    this._router.navigate(['/design/stich-type']);
  }

  updateFormDetail(formData) {
    this._serviceData.update({ id: this.id, ...formData }).subscribe(
      (res) => {
        this._sw.fire("success", "Stich Type", `Record Successfully Update..`);
        this.editable = false;
        this.submitted = false;
        this._router.navigate(["/design/stich-type"]);
      },
      (err) => {
        this._sw.fire("error", "Stich Type", err.error.message);
      }
    );
  }

  ngOnInit(): void {
    this.id = +this.route.snapshot.paramMap.get("id");

    if (this.id > 0) {
      this.getDetailById();
    }
    this.getAllDetail();
    this.makeReactiveForm();

    this.contentHeader = {
      headerTitle: "Design",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Stich Type",
            isLink: true,
            link: "/",
          },
          {
            name: "Information",
            isLink: false,
          },
        ],
      },
    };
  }

  getAllDetail() {
    this._serviceData.getAll().subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      }else if(!res.data && this.tableDataArray.length == 1){
        this.tableDataArray= []
      }
    });
  }

  get f() {
    return this.stichTypeForm.controls;
  }

  getDetailById() {
    this._serviceData.getDetailById({ id: this.id }).subscribe((res) => {
      this.editable = true;
      this.stichTypeForm.patchValue({
        shortName: res.data.shortName,
        name: res.data.name,
        status:res.data.status
      });
    });
  }

  makeReactiveForm() {
    this.stichTypeForm = this.fb.group({
      shortName: [""],
      name: ["", [Validators.required]],
      status:[]
    });
  }

  confirmDelete(id) {
    let deleteId = {
      id: id,
    };
    this._sw.delete(() =>
      this._serviceData.delete(deleteId).subscribe(
        (res) => {
          setTimeout(() => this.getAllDetail(), 2000);
        },
        (err) => {
          this._sw.fire("error", "Collection", "There is a problem");
        }
      )
    );
  }


  searchTerm: string = '';
 get filterTableData() {
    if (!this.searchTerm) {
      return this.tableDataArray;
    }
    return this.tableDataArray.filter(item => item.name.toLowerCase().includes(this.searchTerm.toLowerCase()));
  }
}
